.form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 400px;
  margin: 0 auto;
}

.title {
  text-align: center;
}

.input {
  border: 1px solid var(--border-grey-10);
}

.inputError {
  border: 1px solid var(--primary-red-100);
}

.errorMessage {
  color: var(--primary-red-100);
  margin: 0;
}

.description {
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: var(--secondary-text);
  margin: 0;
}

.resetPassword {
  text-align: right;
}
